import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import uniqid from "uniqid";
import { CartDetailsDiv, CartItemStyled, CartIcon } from "./CartStyled";
import StyledButton from "../common/StyledButton";

function Cart(props) {
  const { cartItems, onExpandCart, cartExpanded } = props;
  const [cartCount, setCartCount] = useState(0);

  useEffect(() => {
    let count = cartItems.reduce(
      (accumulator, current) => +accumulator + current.count,
      0
    );
    setCartCount(count);
  }, [cartItems]);

  return (
    <div>
      <CartIcon onClick={onExpandCart} className={cartExpanded ? "active" : ""}>
        <i className="fas fa-rocket"></i>
        <span>{cartCount}</span>
      </CartIcon>
    </div>
  );
}

function CartDetails(props) {
  const { cartItems, total } = props;
  return (
    <CartDetailsDiv>
      <p>
        Total: <span>${total}</span>
      </p>
      {cartItems.map((item) => {
        if (item.count > 0) {
          return (
            <CartItem
              key={uniqid()}
              fileName={item.fileName}
              image={item.imageFile}
              name={item.name}
              count={item.count}
              price={item.price}
            />
          );
        } else {
          return null;
        }
      })}
      <Link to={process.env.PUBLIC_URL + "/checkout"}>
        {" "}
        <StyledButton>Check Out </StyledButton>
      </Link>
    </CartDetailsDiv>
  );
}

function CartItem(props) {
  return (
    <CartItemStyled>
      <img src={props.image} alt=""></img>
      <Link to={process.env.PUBLIC_URL + "/tours" + props.fileName}>
        {props.fileName}
      </Link>
      <p>
        <span>{props.count}</span> x <span>${props.price}</span>
      </p>
    </CartItemStyled>
  );
}

export { Cart, CartDetails };
