import React, { useState, useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import styled, { ThemeProvider } from "styled-components";

//components
import Nav from "./components/Nav";
import CheckOut from "./components/CheckOut";
import Tours from "./components/Tours";
import Home from "./components/Home";
import TourDetails from "./components/TourDetails";

//data
import toursData from "./assets/toursData.js";

//theme
import mainTheme from "./common/Theme.js";

const StyledApp = styled.div`
  height: 100%;
`;

function App() {
  const [cartItems, setCartItems] = useState(
    toursData.map((tour) => {
      return { ...tour, count: 0 };
    })
  );

  const [totalPrice, setTotalPrice] = useState(0);

  const handleCartChanged = (e, tour, count) => {
    e.preventDefault();
    let newCart = [...cartItems];
    const index = cartItems.findIndex((item) => item.fileName === tour);
    newCart[index].count = count;
    setCartItems(newCart);
  };

  useEffect(() => {
    let price = cartItems.reduce(
      (accumulator, current) => +accumulator + current.count * current.price,
      0
    );
    setTotalPrice(price);
  }, [cartItems]);

  return (
    <ThemeProvider theme={mainTheme}>
      <StyledApp className="App">
        <Nav cartItems={cartItems} total={totalPrice} />
        <Switch>
          <Route path={process.env.PUBLIC_URL + "/checkout"}>
            <CheckOut
              cartItems={cartItems}
              total={totalPrice}
              onAddToCart={handleCartChanged}
            />
          </Route>
          <Route path={process.env.PUBLIC_URL + "/tours/:tourName"}>
            <TourDetails
              toursData={cartItems}
              onAddToCart={handleCartChanged}
            />
          </Route>
          <Route exact path={process.env.PUBLIC_URL + "/tours"}>
            <Tours toursData={toursData} />
          </Route>
          <Route exact path={process.env.PUBLIC_URL + "/"}>
            <Home />
          </Route>
        </Switch>
      </StyledApp>
    </ThemeProvider>
  );
}

export default App;
